import React from "react";
import axios from "axios";
import useAuth from "../../auth/useAuth";
import "./Cuenta.css";

import {
  Navbar,
  Nav,
  Container,
  NavDropdown,
  Form,
  Button,
  Row,
  Col,
  Image,
  Modal,
  ListGroup,
} from "react-bootstrap";

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
  NavLink,
  NavLinkProps,
} from "react-router-dom";
import { useState, useEffect } from "react";

function Cuenta() {
  const { user } = useAuth();

  const [file, setFile] = useState();

  const [imagen, setImagen] = useState();

  const [userName, setUserName] = useState();

  const [tama√±o, setTama√±o] = useState();

  const [pref, setPref] = useState([]);

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [show2, setShow2] = useState(false);
  const [show3, setShow3] = useState(false);

  const handleClose2 = () => setShow2(false);
  const handleShow2 = () => setShow2(true);

  const [animeELection, setAnimeElection] = useState(false);
  const [MangaElection, setMangaElection] = useState(false);
  const [seriesElection, setSeriesElection] = useState(false);

  const [videoElection, setVideoElection] = useState(false);
  const [lightElection, setLightElection] = useState(false);
  const [librosElection, setLibrosELection] = useState(false);

  const [comicELection, setComicElection] = useState(false);
  const [peliELection, setPeliELection] = useState(false);
  const [musicaElection, setMusicaElection] = useState(false);

  const [fileUrl, setFileUrl] = useState(null);
  const [auxP, setAuxP] = useState();

  const [auxC, setAuxC] = useState([]);

  var aux = [];

  var aux = [];

  const handleClose3 = () => {
    setAuxC([]);
    setShow3(false);
  };

  const handleShow3 = () => {
    setShow3(true);
    setAuxC([]);
  };

  const prefe = [
    {
      titulo: null,
      color: "#ff6961",
    },

    {
      titulo: null,
      color: "#77dd77",
    },

    {
      titulo: null,
      color: "#b186f1",
    },
    {
      titulo: null,
      color: "#84b6f4 ",
    },

    {
      titulo: null,
      color: "#fdcae1",
    },

    {
      titulo: null,
      color: "#c5c6c8",
    },
    {
      titulo: null,
      color: "#b2e2f2",
    },

    {
      titulo: null,
      color: "#ffda9e",
    },

    {
      titulo: null,
      color: "#fdf9c4",
    },
  ];

  const prefList = [
    {
      nombre: "Anime",
    },

    {
      nombre: "Manga",
    },

    {
      nombre: "Series",
    },

    {
      nombre: "VideoJuegos",
    },

    {
      nombre: "Libros",
    },

    {
      nombre: "Novelas Ligeras",
    },

    {
      nombre: "Comics",
    },

    {
      nombre: "Peliculas",
    },

    {
      nombre: "Musica",
    },
  ];

  async function onChangeFile(e) {
    setFile(e.target.files[0]);

    const imageFile = e.target.files[0];
    const imageUrl = URL.createObjectURL(imageFile);
    setFileUrl(imageUrl);
  }

  async function getInfo() {
    const res = await axios.get("/api/users/" + user.id);

    const aux1 = res.data;

    const img = aux1.user.profileImage;
    const usuario = aux1.user.userName;
    const preferences = aux1.user.preferences;

    setImagen(img);
    setUserName(usuario);
    setPref(preferences);
    setTama√±o(preferences.length);
    setAuxP(preferences.length);
  }

  useEffect(() => {
    getInfo();
  }, []);

  async function addP(e, id) {
    function delay(n) {
      return new Promise(function (resolve) {
        setTimeout(resolve, n * 1000);
      });
    }
    let element = document.getElementById(id);
    aux.push(id);
    element.className = "animate__animated animate__fadeOut";
    await delay(0.5);
    let padre = await element.parentNode;
    padre.removeChild(element);
  }

  async function saveP() {
    alert(aux);

    await axios.put("/api/users/" + user.id, {
      preferences: aux,
    });

    setAuxP(1);
    getInfo();
  }

  async function updatePreferences() {
    if (animeELection === true) {
      aux.push("Anime");
    }

    if (MangaElection === true) {
      aux.push("Manga");
    }

    if (seriesElection === true) {
      aux.push("Series");
    }

    if (videoElection === true) {
      aux.push("VideoJuegos");
    }

    if (librosElection === true) {
      aux.push("Libros");
    }

    if (lightElection === true) {
      aux.push("Novelas Ligeras");
    }

    if (comicELection === true) {
      aux.push("Comics");
    }

    if (peliELection === true) {
      aux.push("Peliculas");
    }

    if (musicaElection === true) {
      aux.push("Musica");
    }

    await axios.put("/api/users/" + user.id, {
      preferences: aux,
    });

    aux = [null];

    getInfo();

    handleClose3();
    handleClose2();
  }

  useEffect(() => {
    setAnimeElection(false);

    setMangaElection(false);

    setSeriesElection(false);

    setVideoElection(false);

    setLibrosELection(false);

    setLightElection(false);

    setComicElection(false);

    setPeliELection(false);

    setMusicaElection(false);

    for (let i = 0; i < pref.length; i++) {
      if (pref[i] === "Anime") {
        setAnimeElection(true);
      }

      if (pref[i] === "Manga") {
        setMangaElection(true);
      }

      if (pref[i] === "Series") {
        setSeriesElection(true);
      }

      if (pref[i] === "VideoJuegos") {
        setVideoElection(true);
      }

      if (pref[i] === "Libros") {
        setLibrosELection(true);
      }

      if (pref[i] === "Novelas Ligeras") {
        setLightElection(true);
      }

      if (pref[i] === "Comics") {
        setComicElection(true);
      }

      if (pref[i] === "Peliculas") {
        setPeliELection(true);
      }

      if (pref[i] === "Musica") {
        setMusicaElection(true);
      }
    }
  }, [pref, show3]);

  function AE(a) {
    if (a === "Anime" && animeELection === true) {
      return (
        <Button className="eliminarP" onClick={() => eliminar(a)}>
          <i className="icon-eliminar"></i>
        </Button>
      );
    }

    if (a === "Anime" && animeELection === false) {
      return (
        <Button className="agregarP" onClick={() => agregar(a)}>
          <i className="icon-agregar"></i>
        </Button>
      );
    }

    if (a === "Manga" && MangaElection === true) {
      return (
        <Button className="eliminarP" onClick={() => eliminar(a)}>
          <i className="icon-eliminar"></i>
        </Button>
      );
    }

    if (a === "Manga" && MangaElection === false) {
      return (
        <Button className="agregarP" onClick={() => agregar(a)}>
          <i className="icon-agregar"></i>
        </Button>
      );
    }

    if (a === "Series" && seriesElection === true) {
      return (
        <Button className="eliminarP" onClick={() => eliminar(a)}>
          <i className="icon-eliminar"></i>
        </Button>
      );
    }

    if (a === "Series" && seriesElection === false) {
      return (
        <Button className="agregarP" onClick={() => agregar(a)}>
          <i className="icon-agregar"></i>
        </Button>
      );
    }

    if (a === "VideoJuegos" && videoElection === true) {
      return (
        <Button className="eliminarP" onClick={() => eliminar(a)}>
          <i className="icon-eliminar"></i>
        </Button>
      );
    }

    if (a === "VideoJuegos" && videoElection === false) {
      return (
        <Button className="agregarP" onClick={() => agregar(a)}>
          <i className="icon-agregar"></i>
        </Button>
      );
    }

    if (a === "Libros" && librosElection === true) {
      return (
        <Button className="eliminarP" onClick={() => eliminar(a)}>
          <i className="icon-eliminar"></i>
        </Button>
      );
    }

    if (a === "Libros" && librosElection === false) {
      return (
        <Button className="agregarP" onClick={() => agregar(a)}>
          <i className="icon-agregar"></i>
        </Button>
      );
    }

    if (a === "Novelas Ligeras" && lightElection === true) {
      return (
        <Button className="eliminarP" onClick={() => eliminar(a)}>
          <i className="icon-eliminar"></i>
        </Button>
      );
    }

    if (a === "Novelas Ligeras" && lightElection === false) {
      return (
        <Button className="agregarP" onClick={() => agregar(a)}>
          <i className="icon-agregar"></i>
        </Button>
      );
    }

    if (a === "Comics" && comicELection === true) {
      return (
        <Button className="eliminarP" onClick={() => eliminar(a)}>
          <i className="icon-eliminar"></i>
        </Button>
      );
    }

    if (a === "Comics" && comicELection === false) {
      return (
        <Button className="agregarP" onClick={() => agregar(a)}>
          <i className="icon-agregar"></i>
        </Button>
      );
    }

    if (a === "Peliculas" && peliELection === true) {
      return (
        <Button className="eliminarP" onClick={() => eliminar(a)}>
          <i className="icon-eliminar"></i>
        </Button>
      );
    }

    if (a === "Peliculas" && peliELection === false) {
      return (
        <Button className="agregarP" onClick={() => agregar(a)}>
          <i className="icon-agregar"></i>
        </Button>
      );
    }

    if (a === "Musica" && musicaElection === true) {
      return (
        <Button className="eliminarP" onClick={() => eliminar(a)}>
          <i className="icon-eliminar"></i>
        </Button>
      );
    }

    if (a === "Musica" && musicaElection === false) {
      return (
        <Button className="agregarP" onClick={() => agregar(a)}>
          <i className="icon-agregar"></i>
        </Button>
      );
    }
  }

  const agregar = (id) => {
    if (id === "Anime") {
      setAnimeElection(true);
    }

    if (id === "Manga") {
      setMangaElection(true);
    }

    if (id === "Series") {
      setSeriesElection(true);
    }

    if (id === "VideoJuegos") {
      setVideoElection(true);
    }

    if (id === "Libros") {
      setLibrosELection(true);
    }
    if (id === "Novelas Ligeras") {
      setLightElection(true);
    }

    if (id === "Comics") {
      setComicElection(true);
    }

    if (id === "Peliculas") {
      setPeliELection(true);
    }

    if (id === "Musica") {
      setMusicaElection(true);
    }
  };

  function eliminar(id) {
    console.log("owo");
    if (id === "Anime") {
      setAnimeElection(false);
    }

    if (id === "Manga") {
      setMangaElection(false);
    }

    if (id === "Series") {
      setSeriesElection(false);
    }

    if (id === "VideoJuegos") {
      setVideoElection(false);
    }

    if (id === "Libros") {
      setLibrosELection(false);
    }

    if (id === "Novelas Ligeras") {
      setLightElection(false);
    }

    if (id === "Comics") {
      setComicElection(false);
    }

    if (id === "Peliculas") {
      setPeliELection(false);
    }

    if (id === "Musica") {
      setMusicaElection(false);
    }
  }

  function showPreferences() {
    if (auxP === 0) {
      return (
        <>
          <div className="nopref">
            <h5
              style={{
                color: "black",
              }}
            >
              No eres miembro de ninguna categoria
            </h5>
            <Link
              id="plus"
              as={Button}
              style={{ color: "black", display: "inline" }}
              onClick={handleShow2}
            >
              &nbsp;
              <i className="icon-plus" style={{ color: "black" }}></i>{" "}
            </Link>
          </div>

          <Modal show={show2} onHide={handleClose2} animation={false} centered>
            <Modal.Header closeButton>
              <Modal.Title>Modal heading</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              {prefList.map((item) => (
                <div
                  className="add"
                  style={{
                    display: "inline-block",
                    backgroundColor: prefe[i++].color,
                  }}
                  id={item.nombre}
                >
                  <h5 style={{ margin: 0 }}>{item.nombre} </h5>
                  &nbsp;
                  <button onClick={(e) => addP(e, item.nombre)} id="addButton">
                    <i className="icon-plus" id="adios"></i>
                  </button>
                </div>
              ))}
            </Modal.Body>
            <Modal.Footer>
              <Button
                variant="secondary"
                onClick={handleClose2}
                className="modalbuttons"
              >
                Close
              </Button>
              <Button
                variant="primary"
                onClick={(e) => saveP(e)}
                className="modalbuttons"
                type="submit"
              >
                Save Changes
              </Button>
            </Modal.Footer>
          </Modal>
        </>
      );
    } else {
      return (
        <>
          <div
            className="prefCont"
            style={{ backgroundColor: "transparent", width: "100%" }}
          >
            {pref.map((item) => (
              <h5
                className="pref"
                style={{ backgroundColor: prefe[i++].color }}
              >
                {item}
              </h5>
            ))}

            <button
              className="botonEditar"
              onClick={handleShow3}
              style={{ margin: "0px" }}
            >
              {" "}
              <i className="icon-editar" id="editarBoton"></i>
            </button>

            <Modal
              show={show3}
              onHide={handleClose3}
              animation={false}
              centered
            >
              <Modal.Header closeButton>
                <Modal.Title>Modal heading</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <ListGroup>
                  {prefList.map((item) => (
                    <ListGroup.Item className="">
                      <n className="aa" style={{ fontSize: "20px" }}>
                        {item.nombre}
                      </n>
                      {AE(item.nombre)}
                    </ListGroup.Item>
                  ))}
                </ListGroup>
              </Modal.Body>
              <Modal.Footer>
                <Button
                  variant="secondary"
                  onClick={handleClose3}
                  className="modalbuttons"
                >
                  Close
                </Button>
                <Button
                  variant="primary"
                  onClick={(e) => updatePreferences(e)}
                  className="modalbuttons"
                  type="submit"
                >
                  Save Changes
                </Button>
              </Modal.Footer>
            </Modal>
          </div>
        </>
      );
    }
  }

  useEffect(() => {
    if (show === false) {
      setFileUrl(null);
    }
  }, [show]);
  var i = 0;

  async function subir(e) {
    e.preventDefault();
    handleClose();
    const formData = new FormData();
    formData.append("image", file);
    await axios.put(`/api/users/${user.id}/image`, formData);

    setFileUrl(null);

    getInfo();
  }

  function img() {
    if (fileUrl !== null) {
      return <img src={fileUrl} style={{ width: "200px" }} alt="img"></img>;
    }
  }
  return (
    <>
      <section class="seccion-perfil-usuario">
        <div class="perfil-usuario-header">
          <div class="perfil-usuario-portada">
            <div className="container2">
              <img src={imagen} alt="img-avatar"></img>
              <div className="overlay">
                <button className="icon" onClick={handleShow}>
                  <i class="icon-camara"></i>
                </button>

                <Modal
                  show={show}
                  onHide={handleClose}
                  animation={false}
                  centered
                >
                  <Modal.Header closeButton>
                    <Modal.Title>Modal heading</Modal.Title>
                  </Modal.Header>
                  <Modal.Body>
                    <form onSubmit={(e) => subir(e)}>
                      <input
                        type="file"
                        onChange={(e) => onChangeFile(e)}
                      ></input>
                      {img()}
                    </form>
                  </Modal.Body>
                  <Modal.Footer>
                    <Button
                      variant="secondary"
                      onClick={handleClose}
                      className="modalbuttons"
                    >
                      Close
                    </Button>
                    <Button
                      variant="primary"
                      onClick={(e) => subir(e)}
                      className="modalbuttons"
                      type="submit"
                    >
                      Save Changes
                    </Button>
                  </Modal.Footer>
                </Modal>
              </div>
            </div>
          </div>
        </div>
        <div class="perfil-usuario-body">
          <div class="perfil-usuario-bio">
            <h3 class="titulo">{userName}</h3>

            {showPreferences()}
          </div>
          <div class="perfil-usuario-footer">
            <ul class="lista-datos">
              <li>
                <i class="icono fas fa-map-signs"></i> Direccion de usuario:
              </li>
              <li>
                <i class="icono fas fa-phone-alt"></i> Telefono:
              </li>
              <li>
                <i class="icono fas fa-briefcase"></i> Trabaja en.
              </li>
              <li>
                <i class="icono fas fa-building"></i> Cargo
              </li>
            </ul>
            <ul class="lista-datos">
              <li>
                <i class="icono fas fa-map-marker-alt"></i> Ubicacion.
              </li>
              <li>
                <i class="icono fas fa-calendar-alt"></i> Fecha nacimiento.
              </li>
              <li>
                <i class="icono fas fa-user-check"></i> Registro.
              </li>
              <li>
                <i class="icono fas fa-share-alt"></i> Redes sociales.
              </li>
            </ul>
          </div>
          <div class="redes-sociales">
            <a href="" class="boton-redes facebook fab fa-facebook-f">
              <i class="icon-facebook"></i>
            </a>
            <a href="" class="boton-redes twitter fab fa-twitter">
              <i class="icon-twitter"></i>
            </a>
            <a href="" class="boton-redes instagram fab fa-instagram">
              <i class="icon-instagram"></i>
            </a>
          </div>
        </div>
      </section>
    </>
  );
}

export default Cuenta;
